
.cart-table {
    display: grid;
    grid-template-columns: minmax(31em, 13em) minmax(3em, 13em) minmax(17em, 9em) minmax(9em, 9em)  minmax(10em, 9em);
    row-gap: 1em;
    width: fit-content;
    margin: 0 auto;
    background-color: aliceblue;
}

ul {
    display: contents;
}

ul > li {
    display: contents;
}

.table-heading {
    background-color: #333;
    color: white;
}

.table-heading > * {
    background-color: #333;
    padding: 0.5em;
}

.cart-book-title{
    margin-left: -650%;
}

.heading-book {
    grid-column: 1 / 3;
}

.heading-price {
    /*grid-column: 3 / 5;*/
    /*text-align: right;*/
}

.heading-subtotal {
    text-align: right;
    grid-column: -2 / -1;
}

.cart-book-image {
    margin-right: 67%;
    padding: 0 1em;
}

.cart-book-image > * {
    margin-left: auto;
    margin-right: 0;
}

.cart2 img {
    display: block;
    width: 100px;
    height: auto;
}

.rect {
    background-color: var(--primary-color-dark);
}

.narrow-rect {
    width: 75px;
    height: 100px;
}

.square {
    width: 100px;
    height: 100px;
}

.wide-rect {
    width: 125px;
    height: 100px;
}

.cart-book-price {
    padding-left: 1em;
    /*text-align: right;*/
}

.cart-book-quantity {
    padding-left: 1em;
    padding-right: 1em;
}

.cart-book-subtotal {
    text-align: right;
    padding-left: 1em;
    padding-right: 1em;
}


.line-sep {
    display: block;
    height: 2px;
    background-color: rgb(128, 128, 128);
    grid-column: 1 / -1;
}

.icon-button {
    border: none;
    cursor: pointer;
}

.inc-button {
    font-size: 1.125rem;
    color: var(--primary-color);
    margin-right: 0.25em;
}

.inc-button:hover {
    color: var(--primary-color-dark);
}

.dec-button {
    font-size: 1.125rem;
    color: #ccc;
}

.dec-button:hover {
    color: #aaa;
}

.dec-arrow-button,
.inc-arrow-button {
    font-size: 1rem;
    color: var(--primary-color);
}

.dec-arrow-button:hover,
.inc-arrow-button:hover {
    color: var(--primary-color-dark);
}

input[type="number"] {
    width: 4em;
}

select {
    background-color: var(--primary-color);
    color: white;
    border: 2px solid var(--primary-color-dark);
    border-radius: 3px;
}
.row-line {
    border-bottom: 1px solid black;
}

#cartBookImage {
    height: 146px;
    width: 105px;
}

.btn{
    border: none;
    border-radius: 8px;
}

.cart-summary {
    display: flex;
    align-items: center;
    padding: 15px;
    border-radius: 10px;

}

.icon-shape {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #333;
    color: #fff;
    border-radius: 50%;
    margin-right: 15px;
}

.cart-details {
    font-family: 'Arial', sans-serif;
}

.total-items,
.subtotal {
    margin: 8px 0;
    font-weight: bold;
    font-size: 14px;
    color: #333;
}

.clear-cart{
    margin-left: 10px;
    background-color: transparent;
    border-color: #1e4265;
    color: #316da8;
    font-size: 14px;
}

@keyframes sparkle {
    0% {
        background-color: #1e201f;
    }
    50% {
        background-color: #2941a2;
    }
    100% {
        background-color: #3aacb9;
    }
}

.sparkling-button {
    animation: sparkle 1s infinite;
}