/* Confirmation.css */

ul.customerInfoList {
    list-style: none;
    padding: 0;
}

ul.customerInfoList > li {
    display: list-item;
    margin-bottom: 1em;
}

.confirmationView {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 1em auto;
    max-width: 800px;
    padding: 20px;
    border: 1px solid #3498db;
    border-radius: 8px;
    background-color: #f3edf0;
}

.confirmationTable {
    margin-top: 1em;
    margin-bottom: 1em;
    width: 100%;
    border-collapse: collapse;
}

.confirmationTable th,
.confirmationTable td {
    border: 1px solid #3498db;
    padding: 8px;
    text-align: left;
}

.confirmationTable th {
    background-color: #2980b9;
    color: #ffffff;
}

.confirmationTable td {
    background-color: #ffffff;
}

.confirmationTable td:first-child {
    text-align: left;
}

#customerInfo {
    margin-top: 1em;
}

ul.customerInfoList li {
    color: #2c3e50;
}

.confirmationView ul {
    margin-top: 1em;
}

.confirmationView li {
    color: #ac156c;
}
