
.confirmation_table {
    border: 5px solid #3498db;
    width: 75%;
    margin-top: 2em;
    margin-bottom: 2em;

}

.confirmation_td {
    display: table-cell;
    padding: 1em;
    background-color: #ecf0f1;
    vertical-align: middle;
    border: 1px solid #3498db;
}

.confirmation_tr:nth-child(even) > .confirmation_td {
    background-color: #d5dbdb;
}

.confirmation_td:nth-child(1) {
    text-align: left;
}

.confirmation_td:nth-child(2) {
    text-align: center;
}

.confirmation_td:nth-child(3) {
    text-align: right;
}

.confirmation_table th,
.confirmation_table td {
    border: 1px solid #3498db;
    padding: 1em;
}

.confirmation_table th {
    background-color: #2980b9;
    color: #ffffff;
}

.confirmation_table td {
    background-color: #ecf0f1;
}

.confirmation_table td:first-child {
    text-align: left;
}

.confirmation_table td:last-child {
    text-align: right;
}


.confirmation_table .totalRow {
    font-weight: bold;
    background-color: #3498db;
    color: #ffffff;
}
